@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700);
body {
    background: #ffffff;
    font-family: 'Montserrat', serif;
}

html,
body,
#root {
    height: 100%;
    box-sizing: border-box;
    /* overflow-y: hidden; */
}

/* scroll bar  */
body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    border-radius: 10px;
    background-color: #F5F5F5;
}

body::-webkit-scrollbar {
    width: 12px;
    background-color: #F5F5F5;
}

body::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: #cecedc;
}


.main {
    display: flex;
    flex-direction: row;
    align-items: stretch;

}

/* welcome form  */
.button-login {
    margin: 20px;
}

.side-menu {
    width: 200px;
    min-width: 190px;
    z-index: 2;
    top: 0px;
    left: 0px;
    background-color: #574fd8;
    overflow-x: hidden;
    border-radius: 15px 0px 0px 15px;
}

.rightSide {
    flex: auto;
    flex-direction: column;
    justify-content: center;
    flex: 1 1;
    display: flex;
    height: 100%;
    overflow: hidden;
}

/* The navigation menu links */
.side-menu a {
    padding: 14px 8px 14px 26px;
    text-decoration: none;
    font-size: 20px;
    color: #ffffff;
    display: block;
    margin-bottom: 10px;
}

.menu-text {
    display: inline-block;
}

.side-menu a:hover,
.side-menu a:nth-of-type(1) {
    background: linear-gradient(to right, rgba(255, 255, 255, 0.247) 0%, #1C00ff00 100%);
    transition: opacity .35s ease-in-out;
}

.side-menu a svg {
    margin-bottom: -2px;
    padding-right: 10px;
    width: 20px;
    height: 20px;
}

.avatarBox {
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.avatar {
    height: 25%;
}

.avatarBox span {
    margin-top: 10px;
    font-weight: 400;
    color: #ffffff;
}

/* header  */
.header {
    flex: auto;
    height: 190px;
    width: 100%;
    background-color: #ffb5a7;
    z-index: 1;
    border-radius: 0px 15px 0px 0px;
}

.title-heading {
    text-align: right;
    color: #ffffff;
    font-weight: 600;
    padding-right: 40px;
    padding-top: 50px;
}

.subtitle {
    padding-top: 0px;
    margin-top: -15px;
}

.sun {
    color: #ffd21e;
    margin-bottom: -4px;
}

.moon {
    color: #39383d;
    margin-bottom: -4px;
}

.hero-img {
    height: 150%;
    display: block;
    float: left;
    margin-top: -50px;
    transform: scaleX(-1);
    z-index: 0;
}

/* content  */

.content-view {
    flex: auto;
    height: 100%;
    width: 98%;
    z-index: 1;
    background-color: #ffffff;
    padding-right: 20px;
    margin-bottom: 50px;
}

.title-line-text {
    overflow: hidden;
    white-space: nowrap;
    width: 95%;
    display: inline-block;
    padding: 20px 0px 0px 20px;
    font-weight: 900;
    color: #cecedc;
}

.title-line {
    display: inline-block;
    width: 100%;
    margin: 1px 0px 4px 15px;
    font-weight: 900;
    border: 1px solid #cecedc;
}

/* HoritzontalScroll */

.menu-item {
    padding: 0 0px;
    margin: 5px 20px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    border: none;
    border-radius: 15px;
    transition: transform .4s ease;

}

.menu-item:hover {
    transform: scale(1.05);
}

.cover-name {
    text-align: center;
    overflow: hidden;
    white-space: normal;
    white-space: initial;
    max-width: 150px;
    font-weight: 900;
}

.cover-img {
    width: 150px;
    height: auto;
    border-radius: 10px;
}

.menu-item-wrapper {
    height: 220px;
    vertical-align: top;
    margin-top: 40px;
}

.menu-wrapper--inner {
    align-items: flex-start;
}

.scroll-menu-arrow {
    position: absolute;
    margin-top: -280px;
    right: 0;
    padding-right: 50px;
    cursor: pointer;
}

.scroll-menu-arrow:nth-of-type(1) {
    right: 30px;
}

/* media player  */
.music-player-box {
    width: 98%;
    height: 90px;
    background-color: #ffffff;
    position: fixed;
    bottom: 0px;
    opacity: 95%;
    border-radius: 0px 0px 15px 0px;
    z-index: 2;
}

.extraSticky {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    align-items: center;
    width: 90%;
    /* height: 100%; */
    z-index: 2;
    position: -webkit-sticky;
    position: sticky;
}

.extraSticky svg {
    height: 30px;
    width: 30px;
    min-width: 20px;
    margin: 0px 10px 0px 10px;
    cursor: pointer;
    color: #cecedc;
}

.play-circle {
    height: 70px !important;
    width: 70px !important;
    min-width: 40px !important;
    margin: 0px 0px 0px 0px !important;
    color: #574fd8 !important;
}

.play-line {
    width: 80%;
    height: 0px;
    margin: 1px 15px 1px 15px;
    font-weight: 900;
    border: 2px solid #cecedc;
}

.unknown-cover {
    height: 50px;
    width: 50px;
    min-width: 50px;
    background-color: #cecedc;
    border-radius: 7px;
    margin: 0px 10px 0px 20px;
}

.track-name {
    white-space: nowrap;
    font-weight: bold;
    margin: 0px 10px 0px 20px;
}

.dots {
    display: none;
}

.right-icons {
    display: block;
}

@media screen and (max-width: 1625px) {

    .menu-text,
    .unknown-cover,
    .track-name,
    .right-icons {
        display: none;
    }

    .play-line {
        width: 50%;
        margin: 1px 0px 1px 0px;
    }

    .extraSticky {
        width: 80%;

    }

    .dots {
        display: block !important;
        margin-right: 10px;
    }

    .title-heading {
        text-align: left;
        margin-left: 15px;
    }

    .hero-img {
        display: none
    }

    .side-menu {
        width: 15%;
        min-width: 10px;
        text-align: center;
    }

    .avatarBox span {
        display: none;
    }

    .title-line-text {
        width: 80%;
    }

    .scroll-menu-arrow {
        padding-right: 20px;
    }
}

@media screen and (max-width: 510px) {
    .title-heading {
        font-size: 21px;
        padding-top: 30px;
    }

    .subtitle {
        padding-top: 0px;
    }

    .sun,
    .moon {
        width: 21px;
    }

    .side-menu a {
        padding: 14px 8px 14px 12px;
    }

    .avatar {
        height: 15%;
    }

    .header {
        height: 150px;
    }

    .menu-item {
        margin: 0px 50px 0px 40px;
        padding-left: 20px;
    }
}
